//imports dependencies and files
import React, { Component } from "react";
import { Link } from "react-router-dom";
import Navbar from "../../components/Navbar";
import Grid from "../../components/Grid/Grid.js";
import Footer  from "../../components/Footer";
import Imagegrid  from "../../components/Imagegrid";
import { Modal } from 'react-bootstrap';
import Postjob from "../../components/Postjob/Postjob.js";
import JobsList from "../../components/Listjob/JobsList.js";
import {FormGroup, InputGroup, Button, FormControl} from "react-bootstrap";
import LogoutBtn from "../../components/Logout";


class Career extends Component {

  constructor(props) {
      super(props);       
       this.state = {
          isActive:false,
          isToggleOn: false
        }
        this.handleEvents = this.handleEvents.bind(this); 
    }     
     
   toggleModal = () => {
      this.setState({
      isActive:!this.state.isActive
     });
    }

    handleEvents = () => {
      this.setState(function(prevState) {
        return {isToggleOn: !prevState.isToggleOn};
      });
    }  

render() {
    // inline style
       const imagesetting = {
        "width" : "100%",
         "marginTop": "-150px",
        "height" : "auto"
      }
      
      const jumbotronsetting = {
        "backgroundColor" : "white"
      }
    

    return (
      <div>
        
        <Navbar/>
        <LogoutBtn />  
        <div className="row">
            {/*image*/}
            <div className="col-4 imageside">
              <img  alt="image2" src={require("../../images/career.jpg")} style={imagesetting}/>
            </div>
            {/*about career*/}
            <div className="col-8 jobDetailSide">
              <div className="jumbotron jumbotron-fluid" style={jumbotronsetting}>
                <div className="container">

                    <h2 className="display-4">Connecting People.Improving Skills.</h2><hr/>
                    <p className="lead">Youtism is a non-profit organization that exists to educate and connect
                     individuals and families to answers that matter: information and resources that help them make more informed decisions, 
                     build and strengthen relationships, and more importantly, thrive. Together, we’ll help you connect the pieces.</p>
                    
                    {/*3 images*/}
                    <Imagegrid/>
                    
                    {/*toggle button for post jobs*/}
                    <button type="button" className="btn btn-dark btn-lg btn-block" onClick={this.toggleModal}><strong>POST JOBS HERE</strong></button>
                      
                      {/*post job form will open in Modal*/}
                      <Modal show={this.state.isActive} onHide={this.toggleModal}>
                          <Postjob />
                      </Modal>
                      {/*Job list will show up on click */}
                      <button onClick={this.handleEvents} type="button" className="btn btn-primary btn-lg btn-block">
                  <strong> FIND JOBS HERE</strong></button>
                  {this.state.isToggleOn ? <JobsList/>: null}
                </div>
              </div>
            </div>
          </div> {/*end about career*/}

        <div className="jumbotron jumbotron-fluid">
          <div className="container">

            <div className="row">
              <div className="col-md-4"></div>
              <div className="col-md-4">
                <h2 className="text-center">Get job alerts here!</h2>
              </div>
            </div>

            <div className="row">
              <div className="col-md-2"></div>
              <div className="col-md-8">
                <p>Enter your email address to get the latest updates right now in your inbox. If you have a job available and want to post it on our website, please register and then sign in!</p>
              </div>
            </div>

            <div className="row">
                      <div className="col-lg-4"></div>
                      <div className="col-lg-4">
                          <FormGroup>
                              <InputGroup>
                                <FormControl type="email" placeholder="Enter your email address here" style={{"border": "2px solid gray", "border-radius": "20px 0 0 20px", "height": "50px"}}/>
                                  <InputGroup.Button>
                                     <Button type="submit" style={{"background" : "orange", "border": "2px solid gray", "border-radius": "0 20px 20px 0", "height": "50px"}}>Subscribe</Button>
                                  </InputGroup.Button>
                              </InputGroup>
                          </FormGroup>
                      </div>  
            </div>
          </div> 
        </div>  
           
           {/*footer*/} 
          <Footer/>
       </div>        
    );
  }
}
export default Career;


